.worked__body {
  display: flex;
  column-gap: 32px;
  row-gap: 16px;
  padding-inline-end: 30px;
}
@media (max-width: 1025px) {
  .worked__body {
    flex-direction: column;
    gap: 35px;
    padding-inline-end: 0px;
  }
}
@media (max-width: 768px) {
  .worked__body {
    gap: 35px;
  }
}

.worked__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 164px;
}
@media (max-width: 1025px) {
  .worked__container {
    gap: 116px;
    padding-block-start: 105px;
  }
}
@media (max-width: 992.99px) {
  .worked__container {
    flex-direction: column;
    gap: 48px;
  }
}
@media (max-width: 992.99px) and (max-width: 768px) {
  .worked__container {
    padding-block-start: 45px;
  }
}
@media (max-width: 992.99px) and (max-width: 440px) {
  .worked__container {
    align-items: flex-start;
  }
}

.worked__title {
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: -1px;
  max-width: 640px;
}
@media (max-width: 1025px) {
  .worked__title {
    max-width: 524px;
  }
}
@media (max-width: 992.99px) {
  .worked__title {
    text-align: center;
  }
}
@media (max-width: 768px) {
  .worked__title {
    font-size: 32px;
    line-height: 40px;
    text-align: start;
    max-width: 320px;
    letter-spacing: -0.3px;
  }
}

.worked__statistic {
  max-width: 194px;
}
@media (max-width: 768px) {
  .worked__statistic {
    max-width: 350px;
  }
}

.worked__statistic-percent {
  font-size: 40px;
  line-height: 48px;
  font-weight: 700;
  font-family: "Sora", sans-serif;
  padding-block-end: 16px;
}
@media (max-width: 768px) {
  .worked__statistic-percent {
    font-size: 32px;
    line-height: 40px;
  }
}

.worked__statistic-text {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
  color: #88888d;
}